<template>
  <div class="AddressList">
    <!-- 头部 -->
    <div class="navtop"><van-nav-bar title="地址管理" left-arrow @click-left="back"/></div>

    <!-- 地址列表 -->
    <div class="addressList">
      <van-address-list
        :list="list"
        :switchable="false"
        default-tag-text="默认"
        @add="onAdd"
        @edit="onEdit"
      />

      <!-- <div class="img" v-else>
        <img src="../assets/image/AddressEmpty.png">
        <p>还没有地址哟~</p>
      </div> -->
    </div>

  </div>
</template>

<script>
import "../assets/style/addressList.less";
export default {
  name: "AddressList",
  data() {
    return {
      // 地址列表数据
      list: [],

    };
  },
  created() {
    this.getAddressListData();
  },
  methods: {
    // 返回上一级
    back(){
      this.$router.back()
    },

    // 新增地址
    onAdd() {
      this.$router.push({name: 'Address'});
    },

    // 编辑地址
    onEdit(item) {
      this.$router.push({name: 'Address', params: {id:item.id}});
    },

    //查询用户地址列表
    getAddressListData() {
      //获取token
      let tokenString = this.$cookies.get("tokenString");

      //判断token存不存在
      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "GET",
        url: this.baseUrl + "/address/list?page=1&limit=20",
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          this.$toast.clear();

          // 如果没登陆，就跳到登录页面
          if (res.data.status == 410000) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          // 如果登陆，就处理数据
          if (res.data.status === 200) {
            this.list = []
            res.data.data.forEach((v) => {
              this.list.push({
                id: v.id,
                name: v.real_name,
                tel: v.phone,
                address: `${v.province}${v.city}${v.district}${v.detail}`,
                isDefault: Boolean(v.is_default),
              });
            });
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            this.$router.push({ name: "Login" });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },
  },
};
</script>
